
.title {
    display: flex;
    justify-content: center;
    margin-top: 0;

}

.description {
    padding-top: 35vh;
    display: flex;
    justify-content: center;

}

.home-div {
    width: 100%;
    height:100%;
    background: linear-gradient(-45deg, #FDF8E2, #81b46c, #ee99db, #5EA9BE, #9ACDE0, #CBE1EF);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
}
      
@keyframes gradient {
    0% {
     background-position: 0% 50%;
    }
    50% {
     background-position: 100% 50%;
    }
    100% {
    background-position: 0% 50%;
    }
}

/* linear-gradient background courtesy of Alison Quaglia */